interface HeadCell {
  id: string;
  disablePadding: boolean;
  orderField?: keyof Truck;
  getData: (
    row: Truck,
    setMapData: any,
  ) => { data: JSX.Element | string; title?: string };
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: 'truckNumber',
    orderField: 'truckNumber',
    getData: (row) => ({ data: `${row.truckNumber}` }),
    numeric: true,
    disablePadding: false,
    label: 'Truck Number',
  },
  {
    id: 'mainContactInfo',
    getData: (row) => {
      const person = row.coordinator || row.owner;
      let path = '';
      let type = '';
      let firstRow = '';
      let secondRow = '';
      if (person) {
        path = `./${getPath(person)}/view/${person.id}`;
        type = `${getType(person)} `;
        firstRow = `${person.fullName}`;
        secondRow = `${person?.phone}`;
      }
      return {
        data: (
          <span style={{ display: 'flex' }}>
            <span
              style={{
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                textAlign: 'left',
              }}
            >
              <b>{type}</b>
              <a href={path} target="_blank" onClick={stopPropagation}>
                {firstRow}
              </a>
              <br />
              <a
                href={secondRow ? `tel:${secondRow}` : undefined}
                target="_blank"
                onClick={stopPropagation}
              >
                {secondRow || ' '}
              </a>
            </span>
            <IconButton
              size="small"
              onClick={(event) => {
                stopPropagation(event);
                navigator.clipboard.writeText(firstRow + ' ' + secondRow);
              }}
            >
              <ContentCopy sx={{ height: '16px', width: '16px' }} />
            </IconButton>
          </span>
        ),
        title: type + '' + firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Main Contact info',
  },
  {
    id: 'truckInfo',
    getData: (row) => {
      const firstRow = row.type ? `${row.type}` : '';
      const secondRow = row.equipment ? `${row.equipment.join(', ')}` : '';

      return {
        data: (
          <span>
            {firstRow}
            <br />
            {secondRow}
          </span>
        ),
        title: firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Truck info',
  },
  {
    id: 'dimsAndPayload',
    getData: (row) => {
      const firstRowPart1 = row.insideDims ? `${row.insideDims}` : '';
      const firstRowPart2 = row.doorDims ? `${row.doorDims}` : '';
      const secondRow = row.payload ? `${row.payload}` : '';

      return {
        data: (
          <span>
            {(firstRowPart1 ? firstRowPart1 + ' ' : '') +
              `${firstRowPart2 ? '(' + firstRowPart2 + ')' : ''}`}
            <br />
            {secondRow}
          </span>
        ),
        title:
          (firstRowPart1 ? firstRowPart1 + ' Inside ' : '') +
          (firstRowPart2 ? firstRowPart2 + ' Door ' : '') +
          '\n' +
          secondRow +
          ' Payload',
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'DIMS/Payload',
  },
  {
    id: 'status',
    orderField: 'status',
    getData: (row) => {
      const theme = useTheme();
      const status = `${row.status}`;
      const val = (() => {
        switch (status) {
          case 'Will be available':
            return (
              <CatIcon
                style={{
                  height: '24px',
                  width: '24px',
                  color: theme.palette.cat.main,
                }}
              />
              /*<AccessTime
                sx={{ height: '24px', width: '24px', color: grey[900] }}
              />*/
            );
          case 'On route':
            return (
              <TruckIcon
                style={{
                  height: '24px',
                  width: '24px',
                  color: theme.palette.truck.main,
                }}
              />
              /*<LocalShipping
                sx={{ height: '24px', width: '24px', color: orange[500] }}
              />*/
            );
          case 'Available':
            return (
              <Avatar
                sx={{
                  height: '24px',
                  width: '24px',
                  bgcolor: green[500],
                  margin: 'auto',
                }}
              >
                A
              </Avatar>
            );
          case 'Not Available':
            return (
              <Avatar
                sx={{
                  height: '24px',
                  width: '24px',
                  bgcolor: grey[500],
                  margin: 'auto',
                }}
              >
                N
              </Avatar>
            );
          default:
            return status;
        }
      })();

      return {
        data: <span>{val}</span>,
        title: status,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Status',
  },
  {
    id: 'willBeAtLocation',
    getData: (row) => {
      if (row?.availabilityLocation) {
        const secondRow = `${
          row.availabilityAtLocal
            ? dateTimeFormatter.format(
                fromISOCorrected(row.availabilityAtLocal),
              )
            : ''
        }`;

        return {
          data: (
            <LocationForGrid
              location={row.availabilityLocation}
              addInfo={secondRow}
            />
          ),
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'Will be at location',
  },
  {
    id: 'roadsDistance',
    getData: (row) => ({
      data: `${row.milesByRoads?.toFixed(2) || ''}`,
    }),
    numeric: false,
    disablePadding: true,
    label: 'Road distance',
  },
  {
    id: 'location',
    getData: (row, setMapData) => {
      const onRefClick = (event: React.SyntheticEvent) => {
        event.preventDefault();
        stopPropagation(event);
        setMapData([row]);
      };

      if (row?.lastLocation) {
        const secondRow = `${
          row.locationUpdatedAt
            ? dateTimeFormatter.format(new Date(row.locationUpdatedAt))
            : ''
        }`;

        return {
          data: (
            <LocationForGrid
              location={row.lastLocation}
              onRefClick={onRefClick}
              addInfo={secondRow}
            />
          ),
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'Last known location',
  },
  /*{
    id: 'calcDistance',
    getData: (row) => ({
      data: `${row.milesHaversine ? row.milesHaversine.toFixed(2) : ''}`,
    }),
    numeric: false,
    disablePadding: true,
    label: 'Origin away',
  },*/
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order | undefined;
  orderBy: string | undefined;
  rowCount: number;
}

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.background.default,
}));

const StyledTableCell = styled(TableCell)((/*{ theme }*/) => ({
  maxWidth: '100px',
  paddingTop: '6px',
  paddingBottom: '6px',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  overflow: 'hidden',
}));

function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    // onSelectAllClick,
    order,
    orderBy,
    // numSelected,
    // rowCount,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof Truck) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <StyledTableHead>
      <TableRow>
        <StyledTableCell align="center" padding="checkbox">
          Actions
        </StyledTableCell>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'center'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={headCell.orderField && orderBy === headCell.orderField}
              hideSortIcon={!headCell.orderField}
              direction={orderBy === headCell.orderField ? order : 'asc'}
              onClick={
                headCell.orderField
                  ? createSortHandler(headCell.orderField)
                  : () => {
                      return;
                    }
              }
            >
              {headCell.label}
              {headCell.orderField && orderBy === headCell.orderField ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </StyledTableCell>
        ))}
      </TableRow>
    </StyledTableHead>
  );
}

interface EnhancedTableToolbarProps {
  selected: readonly string[];
  setQueryParams: React.Dispatch<
    React.SetStateAction<Record<string, number | string | string[] | Date>>
  >;
}

function EnhancedTableToolbar(props: EnhancedTableToolbarProps) {
  const { selected, setQueryParams } = props;
  // Location field state
  const [locationValue, setLocationValue] = React.useState<
    GeoPoint | undefined
  >(undefined);
  // const [inputValue, setInputValue] = React.useState('');
  // const [locationsQueryParams /*, setLocationsQueryParams*/] = React.useState<{
  // search?: any;
  // }>({});
  // const [options, setOptions] = React.useState<readonly Location[]>([]);
  const [availableFrom, setAvailableFrom] = React.useState<Date | null>(null);
  const [availableTo, setAvailableTo] = React.useState<Date | null>(null);
  const [isFromDateError, setFromDateError] = React.useState<boolean>(false);
  const [isToDateError, setToDateError] = React.useState<boolean>(false);
  const [statusOpen, setStatusOpen] = React.useState<boolean>(false);
  const [statusValue, setStatusValue] = React.useState<TruckStatus[]>([
    'Available',
  ]);
  const [distanceValue, setDistanceValue] = React.useState<number>(200);
  const [searchDataChanged, setSearchDataChanged] =
    React.useState<boolean>(false);

  // const location = useLocation();
  // const navigate = useNavigate();
  const queryClient = useQueryClient();

  const setSnackbarMessage = useSnackbar();

  const { /*mutate,*/ isLoading } = useMutation(TruckDelete, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Deleted',
      });
      queryClient.invalidateQueries(['trucks']);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  /*const setSearch = React.useMemo(() => {
    return debounce((search: string): void => {
      setLocationsQueryParams({ search });
    }, 400);
  }, []);*/
  /*const { data: { items } = { items: [], total: 0 } } = useQuery<
    Result<Location>,
    Error,
    Result<Location>,
    any // ToDo fix type
  >({
    queryKey: ['locations', 0, 15, 'name', 'asc', locationsQueryParams],
    queryFn: modifyedQueryFn(LocationsQuery, true),
    keepPreviousData: true,
    retry: false,
  });*/

  /*React.useEffect(() => {
    if (locationValue) {
      const notSelectedItems = items.filter(
        (item) => item.id !== locationValue.id,
      );
      setOptions([locationValue, ...notSelectedItems]);
    } else {
      if (inputValue === '') {
        setOptions([]);
      } else {
        setOptions([...items]);
      }
    }
  }, [locationValue, items]);*/

  const handleLocationChange = (newValue: GeoPoint | undefined) => {
    setLocationValue(newValue);
    if (newValue) {
      setQueryParams((prev) => ({
        ...prev,
        lastLocation: newValue.join(','),
        distance: distanceValue,
      }));
      setSearchDataChanged(false);
    } else {
      // setInputValue('');
      setQueryParams((prev) => {
        const {
          // lastLocation: _lastLocation,
          // distance: _distance,
          ...rest
        } = prev;
        return rest;
      });
      setSearchDataChanged(false);
    }
    // setSearchDataChanged(true);
  };
  /*const handleLocationInputChange = (event: any, newInputValue: string) => {
    setInputValue(newInputValue);
    setSearch(newInputValue);
  };*/
  const handleFromDateInputChange = (value: Date | null) => {
    setAvailableFrom(value);
    setSearchDataChanged(true);
  };
  const handleToDateInputChange = (value: Date | null) => {
    setAvailableTo(value);
    setSearchDataChanged(true);
  };
  const handleFromDateOnError = (error: any /*, value: any*/) => {
    if (error) {
      setFromDateError(true);
    } else {
      setFromDateError(false);
    }
  };
  const handleToDateOnError = (error: any /*, value: any*/) => {
    if (error) {
      setToDateError(true);
    } else {
      setToDateError(false);
    }
  };
  const handleDateChange = () => {
    /*let from = availableFrom;
    let to = availableTo;
    if (!from && !to) {
      from = new Date(Date.now() + 1000 * 60);
    }
    if (!to && from) {
      to = new Date(from.getTime() + dateLag);
    }
    if (!from && to) {
      from = new Date(to.getTime() - dateLag);
    }
    if (from !== availableFrom) {
      setAvailableFrom(from);
    }
    if (to !== availableTo) {
      setAvailableTo(to);
    }*/
    if (!isFromDateError && !isToDateError) {
      setQueryParams((prev) => {
        const res = { ...prev };
        /*if (from) {
          res.availableAfter = from;
        }
        if (to) {
          res.availableBefore = to;
        }*/
        if (availableFrom) {
          res.availableAfter = availableFrom;
        }
        if (availableTo) {
          res.availableBefore = availableTo;
        }
        return res;
      });
      setSearchDataChanged(false);
    }
  };
  const handleStatusInputChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setStatusValue(event.target.value as unknown as TruckStatus[]);
    setSearchDataChanged(true);
  };
  const handleStatusKeyUp = (event: React.KeyboardEvent) => {
    if (event.key !== 'Enter') {
      return;
    }
    stopPropagation(event);
    handleStatusClose();
  };
  const handleStatusKeyDown = (event: React.KeyboardEvent) => {
    if (event.key !== 'Enter') {
      return;
    }
    stopPropagation(event);
  };
  const handleStatusOpen = () => {
    setStatusOpen(true);
  };
  const handleStatusClose = () => {
    setStatusOpen(false);
    handleStatusChange();
  };
  const handleStatusChange = () => {
    if (statusValue.length && statusValue.length !== TruckStatuses.length) {
      setQueryParams((prev) => ({ ...prev, status: statusValue }));
      setSearchDataChanged(false);
    } else {
      setQueryParams((prev) => {
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        const { status: _, ...rest } = prev;
        return { ...rest };
      });
      setSearchDataChanged(false);
    }
  };
  const handleDistanceInputChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    if (Number.isInteger(Number(event.target.value))) {
      setDistanceValue(Number(event.target.value));
      if (locationValue) {
        setQueryParams((prev) => ({
          ...prev,
          distance: Number(event.target.value),
        }));
        setSearchDataChanged(false);
      }
      // setSearchDataChanged(true);
    }
  };
  const handleClearFilter = () => {
    setLocationValue(undefined);
    setAvailableFrom(null);
    setAvailableTo(null);
    // setStatusValue([...TruckStatuses]);
    setStatusValue(['Available']);
    setDistanceValue(200);
    setQueryParams({ status: ['Available'] });
    setSearchDataChanged(false);
  };
  const handleApplyFilter = () => {
    const queryParams: Record<string, any> = {};
    if (locationValue) {
      queryParams.lastLocation = locationValue.join(',');
      queryParams.distance = distanceValue;
    }
    if (
      statusValue.length !== 0 &&
      statusValue.length !== TruckStatuses.length
    ) {
      queryParams.status = statusValue;
    }

    /*let from = availableFrom;
    let to = availableTo;
    if (!from && !to) {
      from = new Date(Date.now() + 1000 * 60);
    }
    if (!to && from) {
      to = new Date(from.getTime() + dateLag);
    }
    if (!from && to) {
      from = new Date(to.getTime() - dateLag);
    }
    if (from !== availableFrom) {
      setAvailableFrom(from);
    }
    if (to !== availableTo) {
      setAvailableTo(to);
    }
    queryParams.availableAfter = from;
    queryParams.availableBefore = to;*/
    queryParams.availableAfter = availableFrom;
    queryParams.availableBefore = availableTo;

    setQueryParams(queryParams);
    setSearchDataChanged(false);
  };
  // const createHandler = () => {
  //   navigate('./create', {
  //     relative: 'path',
  //     state: { from: location },
  //     replace: false,
  //   });
  // };
  // const editHandler = () => {
  //   navigate(`./edit/${selected[0]}`, {
  //     relative: 'path',
  //     state: { from: location },
  //     replace: false,
  //   });
  // };
  // const deleteHandler = () => {
  //   mutate(`${selected[0]}`);
  // };
  return (
    <Toolbar
      sx={{
        overflow: 'hidden',
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        ...(selected.length > 0 && {
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.activatedOpacity,
            ),
        }),
      }}
    >
      {selected.length > 0 ? (
        <Typography
          sx={{ flex: '1 1 100%' }}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {selected.length} selected
        </Typography>
      ) : (
        <Typography
          sx={{
            flex: '1 1 100%',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden',
          }}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          Trucks Nearby
        </Typography>
      )}
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '350px' },
        }}
      >
        <SearchLocationForGrid
          label={'Enter city, state or zip'}
          value={locationValue}
          setValue={handleLocationChange}
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '200px' },
        }}
      >
        <DateTimePicker
          label="Select available from"
          ampm={false}
          disablePast={true}
          maxDateTime={availableTo ? availableTo : undefined}
          disableIgnoringDatePartForTimeValidation={true}
          viewRenderers={{
            hours: renderTimeViewClock,
            minutes: renderTimeViewClock,
            seconds: renderTimeViewClock,
          }}
          slotProps={{
            textField: {
              fullWidth: true,
              size: 'small',
              id: 'availableFrom',
              name: 'Select available from',
              onBlur: handleDateChange,
            },
          }}
          value={availableFrom}
          onChange={handleFromDateInputChange}
          onClose={handleDateChange}
          onError={handleFromDateOnError}
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '200px' },
        }}
      >
        <DateTimePicker
          label="Select available to"
          ampm={false}
          disablePast={true}
          minDateTime={availableFrom ? availableFrom : undefined}
          disableIgnoringDatePartForTimeValidation={true}
          viewRenderers={{
            hours: renderTimeViewClock,
            minutes: renderTimeViewClock,
            seconds: renderTimeViewClock,
          }}
          slotProps={{
            textField: {
              fullWidth: true,
              size: 'small',
              id: 'availableTo',
              name: 'Select available to',
              onBlur: handleDateChange,
            },
          }}
          value={availableTo}
          onChange={handleToDateInputChange}
          onClose={handleDateChange}
          onError={handleToDateOnError}
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          select
          SelectProps={{
            multiple: true,
            open: statusOpen,
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            //@ts-ignore
            renderValue: (selected: TruckStatus[]) => selected.join(', '),
            onOpen: handleStatusOpen,
            onClose: handleStatusClose,
          }}
          label="Select Status"
          value={statusValue}
          onChange={handleStatusInputChange}
          onKeyUpCapture={handleStatusKeyUp}
          onKeyDownCapture={handleStatusKeyDown}
          onBlur={handleStatusChange}
          size="small"
          fullWidth
          title={statusValue.join(', ')}
        >
          {TruckStatuses.map((status) => (
            <MenuItem key={status} value={status}>
              <Checkbox
                checked={statusValue.indexOf(status as TruckStatus) > -1}
                id={status}
              />
              <ListItemText primary={status} />
            </MenuItem>
          ))}
        </TextField>
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          select
          label="Select Distance"
          value={distanceValue}
          onChange={handleDistanceInputChange}
          size="small"
          fullWidth
        >
          {Array.from(
            { length: (1000 - 50) / 50 + 1 },
            (_, index) => 50 + index * 50,
          ).map((option) => (
            <MenuItem key={option} value={option}>
              {option}
            </MenuItem>
          ))}
        </TextField>
      </Box>
      <Tooltip title="Filter list">
        {searchDataChanged ||
        !(
          locationValue ||
          availableFrom ||
          availableTo ||
          !(statusValue.length === 1 && statusValue[0] === 'Available')
        ) ? (
          <span>
            <IconButton
              disabled={
                ((!locationValue || !distanceValue) &&
                  !(
                    availableFrom ||
                    availableTo ||
                    !(
                      statusValue.length === 1 && statusValue[0] === 'Available'
                    )
                  )) ||
                isFromDateError ||
                isToDateError
              }
              onClick={handleApplyFilter}
            >
              <FilterListIcon />
            </IconButton>
          </span>
        ) : (
          <span>
            <IconButton onClick={handleClearFilter}>
              <FilterListOffIcon />
            </IconButton>
          </span>
        )}
      </Tooltip>
      <Loader visible={isLoading} />
    </Toolbar>
  );
}

export default function EnhancedTable() {
  const [order, setOrder] = React.useState<Order | undefined>(undefined);
  const [orderBy, setOrderBy] = React.useState<keyof Truck | undefined>(
    undefined,
  );
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = React.useState<number>(15);
  const [queryParams, setQueryParams] = React.useState<Record<string, any>>({
    status: ['Available'],
  });
  const tableContainerRef = React.useRef(null);
  const location = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();
  const [, setMapData] = useMap();

  const {
    isLoading,
    isFetching,
    error,
    data: { items, total } = { items: [], total: 0 },
  } = useQuery({
    queryKey: ['trucks', page, rowsPerPage, orderBy, order, queryParams],
    queryFn: TrucksQuery,
    keepPreviousData: true,
    retry: false,
  });
  React.useEffect(() => {
    if (!error) {
      return;
    }
    setSnackbarMessage({
      severity: 'error',
      message: error instanceof Error ? error.message : JSON.stringify(error),
    });
  }, [error]);
  const rows = items || [];

  React.useEffect(() => {
    if (tableContainerRef.current) {
      const container = tableContainerRef.current as HTMLElement;
      container.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    }
  }, [rows]);

  React.useEffect(() => {
    const newSelected: string[] = [];
    selected.forEach((selectedItem) => {
      const selectedRow = rows.find((row) => row.id === selectedItem);
      if (selectedRow) {
        newSelected.push(selectedItem);
      }
    });
    setSelected(newSelected);
  }, [rows]);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    const isDesc = orderBy === property && order === 'desc';
    if (isDesc) {
      setOrder(undefined);
      setOrderBy(undefined);
    } else {
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(property);
    }
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n: Truck) => n.id);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  const handleClick = (
    event: React.MouseEvent<unknown>,
    item: (typeof rows)[number],
  ) => {
    navigate(`./view/${item.id}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };

  /* const handleSelect = (
    event: React.MouseEvent<unknown> /!*, name: string*!/
  ) => {
    event.stopPropagation();
    // const selectedIndex = selected.indexOf(name);
    // let newSelected: readonly string[] = [];
    //
    // if (selectedIndex === -1) {
    //     newSelected = newSelected.concat(selected, name);
    // } else if (selectedIndex === 0) {
    //     newSelected = newSelected.concat(selected.slice(1));
    // } else if (selectedIndex === selected.length - 1) {
    //     newSelected = newSelected.concat(selected.slice(0, -1));
    // } else if (selectedIndex > 0) {
    //     newSelected = newSelected.concat(
    //         selected.slice(0, selectedIndex),
    //         selected.slice(selectedIndex + 1),
    //     );
    // }
    //
    // setSelected(newSelected);
  };*/

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  return (
    <Box sx={{ width: '100%', height: '100%' }}>
      <Paper sx={{ width: '100%', height: '100%', backgroundColor: 'unset' }}>
        <EnhancedTableToolbar
          selected={selected}
          setQueryParams={(query) => {
            setQueryParams(query);
            setPage(0);
          }}
        />
        <TableContainer
          ref={tableContainerRef}
          sx={{ height: 'calc(100% - 115px)' }}
        >
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size="small"
            stickyHeader
          >
            <EnhancedTableHead
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {rows.map((row, index) => {
                const isItemSelected = isSelected(`${row.id}`);
                return (
                  <TableRow
                    hover
                    onDoubleClick={(event) => handleClick(event, row)}
                    role="checkbox"
                    aria-checked={isItemSelected}
                    tabIndex={-1}
                    key={row.id}
                    selected={isItemSelected}
                    sx={{ cursor: 'pointer' }}
                  >
                    <StyledTableCell padding="checkbox">
                      <ReserveTruckButton truck={row} index={index} />
                    </StyledTableCell>
                    {headCells.map((cell) => {
                      const cellData = cell.getData(row, setMapData);
                      return (
                        <StyledTableCell
                          key={cell.id}
                          title={cellData.title}
                          align={cell.numeric ? 'right' : 'center'}
                        >
                          {cellData.data}
                        </StyledTableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15, 20, 25, 35, 50]}
          component="div"
          count={total}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <Loader visible={isLoading || isFetching} />
    </Box>
  );
}
