import * as React from 'react';
import Button from '@mui/material/Button';
import { useMutation, useQueryClient } from '@tanstack/react-query';

import { TruckUpdate } from '../api';
import { Truck } from '../utils/dataTypes';

const reservationTime = 15 * 60 * 1000; // 15 minutes

const calcReserveWillLast = (reservedTill: Date | '' | undefined) => {
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  let reservedWillLast = (reservedTill - new Date()) / (1000 * 60);
  reservedWillLast = reservedWillLast > 0 ? Math.ceil(reservedWillLast) : 0;
  return reservedWillLast;
};

export default function ReserveTruckButton({
  truck,
  index,
}: {
  truck: Truck;
  index: number;
}) {
  const [remainReserved, setRemainReserved] = React.useState<number>(0);
  const { reservedAt, reservedBy } = truck;
  const reservedTill =
    reservedAt && new Date(new Date(reservedAt).getTime() + reservationTime);
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const isReserved = new Date() < reservedTill;

  React.useEffect(() => {
    if (isReserved) {
      setRemainReserved(calcReserveWillLast(reservedTill));
      const interval = setInterval(() => {
        setRemainReserved(calcReserveWillLast(reservedTill));
      }, 1000 * 2);
      return () => {
        clearInterval(interval);
        setRemainReserved(0);
      };
    }
  }, [isReserved, reservedTill]);

  const queryClient = useQueryClient();
  const { mutate /*, isLoading*/ } = useMutation(TruckUpdate, {
    onSuccess: () => {
      queryClient.invalidateQueries({
        queryKey: ['trucks'],
        refetchType: 'active',
      });
      return;
    },
    onError: (/*error: Error*/) => {
      return;
    },
  });

  const reserveButtonClickHandler = (event: React.SyntheticEvent) => {
    event.preventDefault();
    event.stopPropagation();
    mutate({ id: truck.id, reservedAt: isReserved ? null : new Date() });
  };

  const labelId = `enhanced-table-checkbox-${index}`;
  const title = `${
    isReserved && reservedBy
      ? 'Reserved by:\n' +
        reservedBy.fullName +
        '\nTime left: ' +
        remainReserved +
        'min'
      : ''
  }`;
  return (
    <Button
      id={`${labelId}`}
      title={title}
      sx={{ m: 1, height: '35px', lineHeight: 'normal', fontSize: 'x-small' }}
      fullWidth
      variant="contained"
      size="small"
      color="primary"
      onClickCapture={reserveButtonClickHandler}
    >
      {isReserved ? (
        <span>
          Unreserve
          <br />({remainReserved}min)
        </span>
      ) : (
        'Reserve'
      )}
    </Button>
  );
}
